.about {
  background-color: #84a178;
}

.about__subtitle {
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 22px;
  line-height: 1.23;

  text-align: center;
  text-transform: uppercase;

  color: #fcfcfc;
  margin-bottom: 12px;
}

.about__title {
  margin: 0 auto;

  font-family: 'Seymour One', sans-serif;
  font-size: 34px;
  line-height: 1.26;

  text-align: center;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #fcfcfc;
  margin-bottom: 32px;

  @media screen and (min-width: 768px) {
    margin-bottom: 72px;
  }
}
.about__content {
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  @media screen and (min-width: 1200px) {
    justify-content: flex-start;
  }
}

.about__img {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 62px;

  width: 278px;
  height: 278px;
  border: 1px solid rgba(248, 210, 78, 0.7);
  border-radius: 50%;
  background: linear-gradient(
      0deg,
      rgba(248, 210, 78, 0.7),
      rgba(248, 210, 78, 0.7)
    ),
    #d9d9d9;
  box-shadow: 0px 4.48583px 16.8218px rgba(153, 114, 23, 0.25);
  border-radius: 140.182px;

  @media screen and (min-width: 768px) {
    width: 335px;
    height: 331px;
    margin: 0;
    background: linear-gradient(
        0deg,
        rgba(248, 210, 78, 0.7),
        rgba(248, 210, 78, 0.7)
      ),
      #d9d9d9;
    box-shadow: 0px 5.30813px 19.9055px rgba(153, 114, 23, 0.25);
    border-radius: 165.879px;
  }

  @media screen and (min-width: 1200px) {
    width: 500px;
    height: 500px;

    background: linear-gradient(
        0deg,
        rgba(248, 210, 78, 0.7),
        rgba(248, 210, 78, 0.7)
      ),
      #d9d9d9;
    box-shadow: 0px 8px 30px rgba(153, 114, 23, 0.25);
    border-radius: 250px;
  }
}

.about__img::before {
  position: absolute;
  top: 26px;
  left: 0;
  content: ' ';
  width: 100%;
  height: 100%;

  background-image: url(../images/korova-mobile.png);

  @media screen and (min-width: 768px) {
    content: ' ';
    width: 335px;
    height: 331px;

    background-image: url(../images/korova-tablet.png);
  }
  @media screen and (min-width: 1200px) {
    top: 39px;
    left: 2px;
    content: ' ';
    width: 490px;
    height: 490px;

    background-image: url(../images/korova-desktop.png);
    background-repeat: no-repeat;
  }
}

.about__text-item {
  max-width: 220px;
  margin: 0 auto;

  font-size: 12px;
  line-height: 1.76;
  text-align: center;
  color: #fcfcfc;

  @media screen and (min-width: 768px) {
    max-width: 334px;
    text-align: left;
  }

  @media screen and (min-width: 1200px) {
    max-width: 463px;
    line-height: 1.94;
  }
}

.about__text-item:not(:last-child) {
  margin-bottom: 4px;

  @media screen and (min-width: 768px) {
    margin-bottom: 8px;
  }
}

.about__text-item--hidden {
  display: none;

  @media screen and (min-width: 768px) {
    display: block;
  }
}

.about__text-wrap {
  @media screen and (min-width: 768px) {
    margin-left: 32px;
  }

  @media screen and (min-width: 1200px) {
    margin-left: 100px;
  }
}

.about__text-list {
  margin-bottom: 20px;

  @media screen and (min-width: 768px) {
    margin-bottom: 48px;
  }
}

@media screen and (min-width: 1200px) {
  .about__text-item:first-child {
    font-size: 16px;
    line-height: 1.76;
  }

  .about__content {
    margin-right: auto;
  }
}

.about__button {
  width: 164px;
  height: 42px;
  background-color: #f8d24e;
  border-radius: 10px;
  margin-right: auto;
}
